.work-content {
  padding-top: 121px;
  padding-bottom: 103px;

  @include tablet {
    padding-top: 100px;
    padding-bottom: 83px;
  }

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 53px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 33px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;


  }

  &__item {
    grid-column: 4 span;
    display: none;

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 12 span;
    }

    @media screen and (max-width: 355px) {
      grid-column: 11 span;
    }

  }



  &__item.active {
    display: flex;
  }

  &__item.active-more {
    display: flex !important;
  }



}

.load-more {
  display: block;
  padding-top: 68px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  color: var(--grey-color);

  & span {
    position: relative;
    padding-right: 36px;

    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 24px;
      height: 24px;
      background: url('../img/page-work/convert.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;

    }
  }
}