.hero-news {
  background-color: var(--light-color);
  background: url('../img/page-news/hero-news_bg.png');
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 153px;
  padding-bottom: 218px;

  @include tablet {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include small-tablet {
    padding-top: 100px;
    padding-bottom: 60px;
  }

  @include mobile {
    padding-top: 80px;
    padding-bottom: 30px;
  }


}

.hero-post {
  background-color: var(--athens-color);
  padding-top: 153px;
  padding-bottom: 158px;

  @include tablet {
    padding-top: 100px;
  }

  @include small-tablet {
    padding-top: 100px;
  }

  @include mobile {
    padding-top: 80px;
    padding-bottom: 120px;
  }

  &__title {
    padding-top: 39px;
    padding-bottom: 25px;

  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;


    @include mobile {
      flex-direction: column;
      row-gap: 20px;
    }
  }

  &__social-list {
    display: flex;
    gap: 13px;
  }

  &__social-item:hover {
    .hero-post__social-icon {
      fill: var(--primary-color);
    }
  }

  &__social-icon {
    fill: var(--storm-color);
    width: 24px;
    height: 24px;
    opacity: 1;
  }
}